import React from 'react';
import ArticleBlock from '../components/ArticleBlock';
import styled from 'styled-components';

import imgUrl from '../assets/images/加盟頂豐專業火鍋團隊.jpg';

const Container = styled.div`
  display: flex;
  justify-content: center;
`;

const Section = styled.div`
  > .title {
    color: #222;
    font-size: 1.67rem;
    margin: 1rem 0;
  }

  > .highlight {
    color: orange;
    font-weight: bold;
    font-size: 1.4rem;
    text-align: center;
  }
`;

const Divider = styled.div`
  height: 1px;
  background-color: #999;
  width: 100%;
  margin: 2rem 0
`;

const List = styled.ul`
  color: #999;
  list-style-type: cjk-ideographic;
  padding-left: 2rem;
`;

const TableContainer = styled.div`
  padding: 0 2rem;
`;


export default () => {
  return (
    <Container>
      <ArticleBlock title="加盟我們" style={{ textAlign: 'left' }}>
        <Section>
          <h1 className="title">
            加入頂豐團隊絕對是你最大的助力，是你創業的最佳伙伴，輕鬆完成您開店的願望。
          </h1>
          <img src={imgUrl} alt="加盟頂豐"/>
        </Section>
        <Divider />
        <Section>
          <h1 className="title">加入頂豐流程：</h1>
          <List>
            <li>與總公司人員訪談</li>
            <li>市場評估</li>
            <li>簽訂契約</li>
            <li>訂購設備與店內裝潢</li>
            <li>安排教育訓練</li>
            <li>進貨、進設備</li>
            <li>現場模擬</li>
            <li>作業流程實戰（試賣）</li>
            <li>開幕宣傳、促銷，開始營運</li>
          </List>
          <h1 className="highlight">合作年限為三年，續約不另收費！</h1>
        </Section>
        <Divider />
        <Section>
          <h1 className="title">加入頂豐需要：</h1>
          <List>
            <li>強烈企圖心與責任感</li>
            <li>店內空間 25 坪以上（越大越有優勢）</li>
            <li>成立行號（申請統編免用發票）</li>
            <li>開店準備金</li>
          </List>
          <TableContainer>
            <table>
              <thead>
                <tr>
                  <th>方案</th>
                  <th>空間</th>
                  <th>預算</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>A 型</td>
                  <td>30 坪</td>
                  <td>約 140 萬</td>
                </tr>
                <tr>
                  <td>B 型</td>
                  <td>50 坪</td>
                  <td>約 160 萬</td>
                </tr>
                <tr>
                  <td>C 型</td>
                  <td>80 坪</td>
                  <td>約 180 萬</td>
                </tr>
              </tbody>
            </table>
          </TableContainer>
        </Section>
        <Divider />
        <Section>
          <h1 className="title">加盟優勢與利基：</h1>
          <List>
            <li>湯頭味美，不口渴，不怕比較。</li>
            <li>食材料多、新鮮才敢給客人煮（不同於一般臭臭鍋店，鍋淺料少且煮好才上鍋）</li>
            <li>乾淨、舒適的座位與環境，親切熱忱的服務，其他業者沒得比！</li>
            <li>標準S.O.P作業，簡單教學，只要4~6天，反覆練習即會。</li>
            <li>前置作業、出餐作業、收銀作業、會計作業與營業報表…整套傳授，沒做過生意沒經驗者也能迅速上手。</li>
            <li>銅盤與火鍋不油不膩，一年四季皆可販售。</li>
            <li>專屬商圈範圍，保障營業區域。務讓每一分店都能穩定獲利至為最重要。</li>
            <li>整套營運方法，開店穩定後即使老闆沒上班，員工照樣如常營業，不受影響。</li>
          </List>
        </Section>
      </ArticleBlock>
    </Container>
  );
};



